import IncreaseIndentButton from './components/IncreaseIndentButton';
import DecreaseIndentButton from './components/DecreaseIndentButton';
import IndentButton from './components/IndentButton';

export default function toolbar(indentConfig) {
  const indentLocaleConfig = indentConfig.locale;
  return {
    buttons: {
      default: IndentButton,
      decrease: DecreaseIndentButton,
      increase: IncreaseIndentButton,

      // For backward-compatibility
      left: DecreaseIndentButton,
      right: IncreaseIndentButton,
    },
    locale: indentLocaleConfig,
  };
}
